import { Transition } from "react-transition-group";
import { useEffect, useState } from "react";

import { Button, IconButton } from "../../ui/button/Button.styled";
import { Text, Heading } from "../../ui/text/text.styled";
import {
  TextWrapper,
  MainPhoto,
  DisplayWrapper,
  MainText,
  IconButtonsWrapper,
  LinkInfo,
  ArrowButton,
} from "./MainDisplay.styled";
import {
  TelegramIcon,
  UpArrowIcon,
  VKIcon,
  WhatAppIcon,
} from "../../ui/icons/Icons";
import { IconButtons } from "../../layout/icon-buttons/IconButtons";
import { Link } from "react-scroll";

const linksInfo = {
  telegram: "  Telegram",
  whatsapp: "  WhatsApp",
  vk: " VK",
  default: "",
};

export const MainDisplay = ({ animation }) => {
  const [animate, setAnimate] = useState(true);
  const [hoveredLink, setHoveredLink] = useState("default");

  useEffect(() => {
    setAnimate(true);
    setTimeout(() => {
      setAnimate(false);
    }, 500);
  }, []);

  return (
    <DisplayWrapper>
      <Transition in={animate} timeout={200}>
        {(state) => <MainPhoto state={state} />}
      </Transition>
      <Transition in={animate} timeout={0}>
        {(state) => (
          <TextWrapper state={state}>
            <Heading>  </Heading>
            <Text>
                       
                     
               .     
                     
              .        
              ,     .
            </Text>
            <Button buttonWidth={"200px"} font="20px">
               
            </Button>
            <IconButtons animation={false} setHoveredLink={setHoveredLink} />
            <LinkInfo>{linksInfo[hoveredLink]}</LinkInfo>
          </TextWrapper>
        )}
      </Transition>
      {!animation && (
        <Link to="main" spy={true} smooth={true} duration={500}>
          <ArrowButton>
            <UpArrowIcon />
            <p></p>
          </ArrowButton>
        </Link>
      )}
    </DisplayWrapper>
  );
};
