import { useState } from "react";
import { DisplayWrapper } from "../index.styled";
import {
  Container,
  List,
  Wrapper,
  Row,
  SvgWrapper,
  ButtonWrapper,
} from "./PlasticAcceptance.styled";
import { Heading, Text } from "./../../ui/text/text.styled";
import {
  BackArrow,
  BottleFour,
  BottleOne,
  BottleThree,
  BottleTwo,
} from "../../ui/icons/Icons";
import { Button } from "../../ui/button/Button.styled";
import { Transition } from "react-transition-group";

export const PlasticAcceptance = ({ animation }) => {
  const [step, setStep] = useState(1);

  const handleSetNextStep = () => setStep(step + 1);
  const handleSetPrevStep = () => setStep(step - 1);

  return (
    <DisplayWrapper>
      {step === 1 && (
        <Transition in={animation && step === 1} timeout={200}>
          {(state) => (
            <Wrapper>
              <Row content={"start"} state={state}>
                <Container>
                  <Heading>   ?</Heading>
                  <List>
                          
                         . ,
                     10%     
                     3%.  ,   
                    ,    ,   
                       30%.
                    <br />
                      ,   
                        ,   .
                  </List>
                </Container>
                {/* <SvgWrapper number={0}>
                  <BottleOne />
                </SvgWrapper>
                <SvgWrapper number={1}>
                  <BottleTwo />
                </SvgWrapper>
                <SvgWrapper number={2}>
                  <BottleThree />
                </SvgWrapper> */}
                <ButtonWrapper buttonWidth="300px">
                  <Button font="20px" onClick={handleSetNextStep}>
                     ?
                  </Button>
                </ButtonWrapper>
              </Row>
            </Wrapper>
          )}
        </Transition>
      )}
      {step === 2 && (
        <Transition in={animation && step === 2} timeout={200}>
          {(state) => (
            <Wrapper>
              <Row state={state}>
                <Container>
                  <Heading></Heading>
                  <List>
                    &#8226; -  5  (, ,
                    ,    ,  
                     ,     
                    )
                    <br />
                    &#8226;, ,    , ,
                        ..
                    <br />
                    &#8226;-,  ,  
                    <br />
                    &#8226;  19  
                    <br />
                    &#8226;   
                  </List>
                </Container>
                <Container>
                  <Heading> </Heading>
                  <List>
                    &#8226; 
                    <br />
                    &#8226;
                    <br />
                    &#8226;
                    <br />
                    &#8226;    4, 5, 6, 7, 8
                  </List>
                  <ButtonWrapper buttonWidth="500px">
                    <Button
                      font="20px"
                      onClick={handleSetNextStep}
                      buttonWidth="250px"
                    >
                       ?
                    </Button>
                  </ButtonWrapper>
                </Container>
              </Row>
            </Wrapper>
          )}
        </Transition>
      )}
    </DisplayWrapper>
  );
};
